      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :show-close="false"
      :visible.sync="dialogChoseStatus"
      width="600px"
    >
      <el-row>
        <el-form :model="form" ref="form" :rules="rules" style="width: 500px; margin: 0 auto;">
          <el-form-item label="选择性别" prop="sex">
            <el-radio-group v-model="form.sex">
              <el-radio :value="1" label="男"></el-radio>
              <el-radio :value="2" label="女"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="选择年龄" prop="age">
            <el-radio-group v-model="form.age">
              <el-radio :value="1" label="20岁以下"></el-radio>
              <el-radio :value="2" label="20-27岁"></el-radio>
              <el-radio :value="3" label="27岁以上"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="婚姻状况" prop="marriage" v-if="form.sex==='女'&&form.age==='20-27岁'">
            <el-radio-group v-model="form.marriage">
              <el-radio :value="1" label="单身待解救"></el-radio>
              <el-radio :value="2" label="热恋中"></el-radio>
              <el-radio :value="3" label="失恋中"></el-radio>
              <el-radio :value="4" label="已婚人士"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="选择职业" prop="job">
            <el-radio-group v-model="form.job">
              <el-radio label="政府行业" :value="1"></el-radio>
              <el-radio label="IT行业" :value="2"></el-radio>
              <el-radio label="教育行业" :value="2"></el-radio>
              <el-radio label="其他行业" :value="3"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
      </el-row>
      <el-row style="text-align: right;">
        <el-button size="small" @click="dialogChoseStatus=false">取 消</el-button>
        <el-button size="small" type="primary" @click="handleClose('form')">确 定</el-button>
      </el-row>
    </el-dialog>
    <el-row v-if="textShow===2" style="text-align: center; color: #666;margin-top: 30px;">
      <p>小生不才,</p>
      <p>斗胆一问, 不知姑娘是否心系别家公子？</p>
      <p>感情之事在下不敢儿戏。</p>
      <p>如若姑娘早以心系他人，在下便不再打扰。</p>
      <p>如若有所唐突，还望姑娘多加体凉</p>
      <p>若姑娘非我良人那在下就不庸人自恼。</p>
      <p>在下已经越陷越深。还望姑娘尽早告知。</p>
      <p>莫让我如此不堪。</p>
      <p>话已至此我便先在此谢过。</p>
    </el-row>
    <el-row v-if="textShow===3" style="text-align: center; color: #666;margin-top: 30px;">
      <p>小生不才,</p>
      <p>有幸在此遇见姑娘，</p>
      <p>见姑娘性趣相投，</p>
      <p>不愿错过姑娘，</p>
      <p>愿与姑娘携手看日月星辰更替直到白头可好，</p>
      <p>赏万里繁华走过春夏秋冬可愿。</p>
      <p>Mr_fayu@163.com/Mr_fayu@qq.com</p>
    </el-row>
    <el-row v-if="textShow===1||textShow===0" class="picShow">
      <el-row type="flex" justify="space-around" class="font-size--large">
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
      </el-row>
      <el-row type="flex" justify="space-between">
        <el-row type="flex" style="flex-direction: column;" justify="space-around" class="font-size--large">
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
        </el-row>
        <el-row class="ptb-20">
          <el-row type="flex" justify="center" style="flex-wrap: wrap;">
            <img src="img/author/blank1.png" alt="空">
            <img src="img/author/blank2.png" alt="空">
            <img src="img/author/blank3.png" alt="空">
            <img src="img/author/blank4.png" alt="空">
          </el-row>
          <el-row class="line"></el-row>
          <el-row type="flex"
                  v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}"
                  justify="space-around">
            <img src="img/author/wx.png" alt>
            <img src="img/author/zfb.jpg" alt>
          </el-row>
          <el-row style="text-align: center; color: #666;padding: 10px 0;">感谢各位大佬的捐赠， 有钱了就去买个自己的服务器。⊙ω⊙</el-row>
        </el-row>
        <el-row type="flex" style="flex-direction: column;" justify="space-around" class="font-size--large">
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
          <span>😙</span>
        </el-row>
      </el-row>
      <el-row type="flex" justify="space-around" class="font-size--large">
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
        <span>😙</span>
      </el-row>
    </el-row>
  </el-row>
</template>
<script>
export default {
  name: "author",
  data() {
    return {
      dialogChoseStatus: false,
      form: {
        sex: "",
        age: "",
        job: "",
        marriage: ""
      },
      rules: {
        sex: [{ required: true, message: "请选择您的年龄", trigger: "blur" }],
        age: [{ required: true, message: "请选择您的年龄", trigger: "change" }],
        job: [{ required: true, message: "请选择工作性质", trigger: "change" }],
        marriage: [
          { required: true, message: "请选择婚姻状况", trigger: "change" }
        ]
      },
      textShow: 0
    };
  },
  mounted() {},
  methods: {
    vueClick (e) {// 鼠标点击特效
      /**
       * 根据鼠标点击坐标初始化dom，通过过渡属性实现动画效果
       * dom渲染完成后设置目标偏移位置及目标透明度
       * 过渡结束后移除dom
       * @author:slzs
       */
      let symbol = document.createElement('b')
      symbol.style.position = 'absolute'
      symbol.style.left = (e.pageX - 10) + 'px'
      symbol.style.top = (e.pageY - 15) + 'px'
      symbol.style.zIndex = '9999'
      symbol.style.userSelect = 'none' // 禁止文本被选中，影响美观
      symbol.style.fontSize = '18px'
      symbol.style.color = `rgb(${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)})` // 随机颜色
      symbol.style.transition = 'all 1.5s'
      symbol.addEventListener('transitionend', function (et) { // 动画结束移除dom
        if (et.propertyName === 'opacity' && et.srcElement.style.opacity === 0)
          et.srcElement.remove()
      })
      // 轮换几个预制字符
      /**
       * დღ♡❣❤❥❦❧♥
       * ♩♪♫♬♭♮♯
       * ϟ☀☁☂☃☄☉☼☽☾♁♨❄❅❆
       * ☠☤☥☦☧☨☩☪☫☬☮☭☯☸☽☾♕♚♛✙✚✛✜✞✠✡✢卍卐
       * ♈♉♊♋♌♍♎♏♐♑♒♓
       * ☹☺☻☿♀⚣⚤⚥⚦⚧〠㋡
       * ㊎㊍㊌㊋㊏
       * 🙈🙉🙊🐵🐒🐶🐕🐩🐺🐱😺😸😹😻😼😽🙀😿😾
       * 🐈🐯🐅🐆🐴🐎🐮🐂🐃🐄🐷🐖🐗🐽🐏🐑🐐🐪🐫🐘🐭🐁🐀🐹🐰🐇🐻🐨🐼🐾
       * 🐔🐓🐣🐤🐥🐦🐧🐸🐊🐢🐍🐲🐉🐳🐋🐬🐟🐠🐡🐙🐚🐌🐛🐜🐝🐞🦋
       * 💐🌸💮🌹🌺🌻🌼🌷🌱🌲🌳🌴🌵🌾🌿🍀🍁🍂🍃
       * 🌍🌎🌏🌐🌑🌒🌓🌔🌕🌖🌗🌘🌙🌚🌛🌜☀🌝🌞⭐🌟🌠☁⛅☔⚡❄🔥💧🌊
       * 💩🍇🍈🍉🍊🍋🍌🍍🍎🍏🍐🍑🍒🍓🍅🍆
       * 🌽🍄🌰🍞🍖🍗🍔🍟🍕🍳🍲🍱🍘🍙🍚🍛🍜🍝🍠🍢🍣🍤
       * 🍥🍡🍦🍧🍨🍩🍪🎂🍰🍫🍬🍭🍮🍯🍼☕🍵🍶🍷🍸🍹🍺🍻🍴
       */
      switch (++this.clickCount) {
        case 1:
          symbol.innerText = '♈'
          break
        case 2:
          symbol.innerText = '♉'
          break
        case 3:
          symbol.innerText = '♊'
          break
        case 4:
          symbol.innerText = '♋'
          break
        case 5:
          symbol.innerText = '♌'
          break
        case 6:
          symbol.innerText = '♍'
          break
        case 7:
          symbol.innerText = '♎'
          break
        case 8:
          symbol.innerText = '♏'
          break
        case 9:
          symbol.innerText = '♐'
          break
        case 10:
          symbol.innerText = '♑'
          break
        case 11:
          symbol.innerText = '♒'
          break
        case 12:
          symbol.innerText = '♓'
          break
        default:
          symbol.innerText = '❤'
          this.clickCount = 0
      }
      document.body.appendChild(symbol)
      requestAnimationFrame(() => {
        symbol.style.top = (e.pageY - 100) + 'px'
        symbol.style.opacity = 0
      })
    },
    handleClose(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.form);
          let status =
            "您是一位" +
            this.form.age +
            "的" +
            this.form.sex +
            "性" +
            this.form.job +
            "工作者";
          this.$confirm(status, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "success"
          })
            .then(() => {
              this.dialogChoseStatus = console.log(
                "this.form.marriage",
                this.form.marriage
              );
              if (!this.form.marriage) {
                this.textShow = 1;
              } else if (this.form.marriage === "已婚人士") {
                this.textShow = 1;
              } else if (this.form.marriage === "热恋中") {
                this.textShow = 2;
              } else {
                this.textShow = 3;
              }
            })
            .catch(() => {
              this.resetForm("form");
            });
        } else {
          // console.log('error submit!!')
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>
<style lang="scss">
.author {
  .picShow {
    padding-top: 20px;
    img {
      height: 150px;
      margin: 10px;
    }
  }
  .line {
    height: 1px;
    background: #ccc;
    width: 100%;
    margin: 20px 0;
  }
}
</style>

